.fraud-container {
  width: 700px;
  margin: 0 auto;
}

.fraud-config {
  border-radius: 2px;
  background-color: var(--var-background-color);
  padding: 20px;
  padding: 20px;
  margin: 5px;
}

.fraud-container p {
  font-weight: 300;
  line-height: 24px;
}

.fraud-container h3 {
  font-weight: 300;
  margin-bottom: 10px;
}

.fraud-box {
  display: inline-block;
  width: 340px;
  border-radius: 2px;
  background-color: var(--var-background-color);
  padding: 20px;
  margin: 5px;
  min-height: 250px;
  vertical-align: top;
  cursor: pointer;
}

.fraud-box:hover,
.selected {
  background-color: var(--var-chargedback-color);
}
