.op-page {
    padding: 30px 10px;
    margin-top: 155px;
}
.op-page-container{
  height: 100%;
  position: relative;
}
.op-page-body {
  height: 100%;
}


@media screen and (max-width: 1099px){
  .op-page {
    margin-top: 135px;
  }
}
@media screen and (max-width: 875px){
    .op-page {
      padding: 30px 0;
    }
}
@media screen and (max-width: 480px){
    .op-page {
      margin-top: 120px;
  }
}