.chameleon-logo {
  height: 50px;
}

.chameleon-logo-text {
  fill: var(--var-well-color);
}

.op-navbar {
  background-color: var(--var-bgNavbar-color);
  padding: 17.5px 0 12.5px;
  position: relative;
  z-index: 999;
  color:var(--var-textNavbar-color);
}

.op-navbar .dropdown{
  position: relative;
}

.navbar-right, .navbar-left {
  display: inline-block;
  vertical-align: middle;
  width: 50%;
}

.navbar-right{
  text-align: right;
}

.navbar-right .username {
  font-size: 18px;
  padding-right: 20px;
  position: relative;
  cursor: pointer;
}

span.username:after {
  font-family: fontAwesome;
  font-weight: 900;
  content: '\f107';
  position: absolute;
  right: 0;
  top: 2px;
}

a.op-navbar-brand{
  height: 30px;
  display: inline-block;
}
a.op-navbar-brand img, .chameleon-logo{
  height: 100%;
  display: block;
}