
.dropdown-content {
  position: absolute;
    right: -50px;
    background: var(--var-profilemenu-color);
    padding:  13px;
    z-index: 9;
    border: 1px solid var(--tile-border-color);
    font-size: 13px;
    box-shadow: 0px 0px 4px 0px rgba(0,0,0,0.3);
    top: 30px;
    width: 175px;
    text-align: left;
    border-radius: 2px;
} 
.dropdown-content label {
  width: 100%;
  display: inline-block;
  margin-bottom: 15px;
}

.dropdown-content label:last-child {
  margin-bottom: 0;
}
.dropdown-content:after {
  content: '';
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-bottom: 22px solid var(--var-profilemenu-color);
  position: absolute;
  top: -15px;
  right: 40px;
}
.dropdown-content label a {
  color: var(--var-link-color);
  font-size: 15px;
  vertical-align: middle;
  font-weight: 500;
}

.dropdown-content label img {
  vertical-align: middle;
  margin-right: 8px;
}
.help-container img {
  width: 22px;
}
.help-container, .dropdown-username {
  display: inline-block;
  vertical-align: middle;
}
.help-container{
  padding-right: 20px;
  position: relative;
}
.dropdown-username{
  padding-left: 20px;
}
.dropdown > .help-container:after {
  content: '';
  position: absolute;
  width: 1px;
  height: 40px;
  background: var(--var-white-color);
  right: 0;
  top: -10px;
}
@media screen and (max-width: 1099px){
  .dropdown-content {
    right: -15px;
  } 
}