.op-tile-2x {
  display: inline-block; 
  width: calc(100% - 25% - 7px);
  background-color: rgba(255,255,255,0.5);
  padding: 25px 28px 0;
  position: relative;
  border: 2px solid var(--tile-border-color);
  border-radius: 10px;
  color: var(--var-text-color);
}
.op-tile-2x .transaction-box {
  min-height: 450px;
  width: 100%;
  padding: 0;
  height: auto;
}
.op-tile-2x .transactions-widget tr:last-child{
    padding-bottom: 0;
}
.op-tile-2x-title {
  font-weight: 600;
  font-size: 20px;
  width: calc(100% - 120px);
  display: inline-block;
  letter-spacing: -0.5px;
}
.op-tile-2x-head p {
  width: 120px;
  text-align: right;
  display: inline-block;
  font-size: 15px;
  font-weight: 500;
  color: var(--var-transactionSeeAll-color);
  outline: none;
}
.transaction-report .op-tile-2x-head {
  padding-top: 0;
}
.op-tile-2x-options {
  position: absolute;
  top: 0;
  right: 0;
  padding: 5px;
}
.op-tile-2x-footer {
  position: absolute;
  bottom: 0;
  left: -20px;
  width: 100%;
  text-align: right !important;
  padding: 10px;
  font-size: 12px;
  text-transform: uppercase;
}
