}
button.btn.btn-cancel {
    background: transparent;
    color:var(--var-toggleHighlight-color);
}
button.btn.btn-save {
    color:var(--var-white-color);
    background: var(--var-toggleHighlight-color);
}
button.btn.btn-cancel:hover{
    color:var(--var-primary-color);
    border: 1px solid var(--var-primary-color);
}

button.btn.btn-save:hover{
    background:var(--var-primary-color);
    border: 1px solid var(--var-primary-color);
}

.my-collapsible__padding .switch-toggle-container > p, .my-collapsible__padding .switch-toggle-container > .onoffswitch {
    display: inline-block;
    vertical-align: middle;
    margin: 0;
}
.my-collapsible__padding .switch-toggle-container > p {
    width: calc(100% - 42px);
}

.onoffswitch {
    position: relative; width: 42px;
    -webkit-user-select:none; -moz-user-select:none; -ms-user-select: none;
}
.onoffswitch-checkbox {
    display: none;
}
.onoffswitch-label {
    display: block; overflow: hidden; cursor: pointer;
    border-radius: 20px;
}
.onoffswitch-inner {
    display: block; width: 200%; margin-left: -100%;
    transition: margin 0.3s ease-in 0s;
}
.onoffswitch-inner:before, .onoffswitch-inner:after {
    display: block; float: left; width: 50%; height: 22px; padding: 0; line-height: 22px;
    box-sizing: border-box;
}
.onoffswitch-inner:before {
    content: "";
    padding-left: 10px;
    background-color: var(--var-toggleActive-color);
}
.onoffswitch-inner:after {
    content: "";
    padding-right: 10px;
    background-color: var(--var-toggleInactive-color);
    text-align: right;
}
.onoffswitch-switch {
    display: block;
    width: 18px;
    margin: 2px;
    background: #FFFFFF;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 20px;
    border-radius: 50%;
    transition: all 0.3s ease-in 0s;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
    margin-left: 0;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
    right: 0px; 
}
input.input-email-phone {
    font-size: 14px;
    display: inline-block;
    width: 100%;
    color: var(--var-slidetext-color);
    border: 1px solid var(--tile-border-color);
    background: var(--var-white-color);
    padding: 10px 15px;
    border-radius: 5px;
    margin-top: 10px;
}
.my-collapsible-margin {
    margin: 10px 0;
    display: inline-block;
    width: 100%;
}
p.my-collapsible-title.text-blue {
    margin-bottom: 5px;
}

.api-key-container textarea {
    width: 100%;
    display: inline-block;
    border: 1px solid var(--tile-border-color);
    background: var(--var-white-color);
    color: var(--var-slidetext-color);
    overflow: hidden;
    padding: 10px 15px;
    min-height: 70px;
    resize: none;
}

.copy-key {
    font-size: 8px;
    color: var(--var-toggleHighlight-color);
    font-weight: 600;
    position: relative;
    padding-right: 16px;
    display: inline-block;
    cursor: pointer;
    line-height: 13px;
}
span.copy-key:after {
    content: '';
    width: 12px;
    height: 12px;
    border: 1px solid var(--var-toggleHighlight-color);
    position: absolute;
    right: 0;
    top: 0px;
}
.api-key-container p.subtext {
    width: calc(100% - 58px);
    display: inline-block;
}
span.copy-key:before {
    content: '';
    width: 12px;
    height: 12px;
    border: 1px solid var(--var-toggleHighlight-color);
    position: absolute;
    right: -2px;
    top: -2px;
}

.setting-risk-filter .slidecontainer {
    width: 100%;
}
  
.setting-risk-filter input[type=range] {
    -webkit-appearance: none;
    width: 100%;
    height: 4px;
    background: var(--var-toggleInactive-color);
    outline: none;
    border-radius: 2px;
    margin-bottom: 15px;
}
.setting-risk-filter input[type=range]::-ms-fill-lower {
    background:var(--var-toggleInactive-color);
}
.setting-risk-filter input[type=range]::-ms-fill-upper {
    background:var(--var-toggleInactive-color);
}
.setting-risk-filter input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 17px;
    height: 17px;
    border-radius: 50%;
    background: var(--var-white-color);
    border: 2px solid var(--var-toggleHighlight-color);
    cursor: pointer;
}
  
.setting-risk-filter input[type=range]::-moz-range-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 17px;
    height: 17px;
    border-radius: 50%;
    background: var(--var-white-color);
    border: 2px solid var(--var-toggleHighlight-color);
    cursor: pointer;
}
  
.setting-risk-filter input[type=range]::-ms-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 17px;
    height: 17px;
    border-radius: 50%;
    background: var(--var-white-color);
    border: 2px solid var(--var-toggleHighlight-color);
    cursor: pointer;
}


input[type=range]::-moz-range-thumb {
    width: 17px;
    height: 17px;
    border-radius: 50%;
    background: var(--var-white-color);
    border: 2px solid var(--var-toggleHighlight-color);
}

/*hide the outline behind the border*/
input[type=range]:-moz-focusring{
    outline: 1px solid white;
    outline-offset: -1px;
}

input[type=range]::-ms-fill-lower {
    background: var(--var-white-color);
    border-radius:  50%;
}
input[type=range]::-ms-fill-upper {
    background: var(--var-white-color);
    border-radius:  50%;
}
input[type=range]::-ms-thumb {
    border: none;
    width: 17px;
    height: 17px;
    border-radius: 50%;
    background: var(--var-white-color);
}
input[type=range]:focus::-ms-fill-lower {
    background: #888;
}
input[type=range]:focus::-ms-fill-upper {
    background: #ccc;
}


span.copy-key p.copied {
    display: none;
}
.copy-key.copied p.copied {
    position: absolute;
    display: block;
    background: #299de0;
    font-size: 8px;
    color: #fff;
    padding: 2px 10px;
    border-radius: 15px;
    bottom: -30px;
    right: -5px;
}
.scroll-bar > div {
    overflow-x: hidden !important;
}

.unsave-changes-warning {
    background-color: var(--var-toggleUnsave-color);
    padding: 10px 30px;
    color: var(--var-text-color);
    font-size: 16px;
    line-height: 20px;
    text-align: center;
}

 
.save-changes-success {
    background-color:var(--var-toggleSave-color);
    padding: 10px 30px;
    color:var(--var-text-color);
    font-size: 16px;
    line-height: 20px;
    text-align: center;
}

.loading-block {
    position: absolute;
    background-color: rgba(0,0,0, 0.3);
    width: 100%;
    height: 100%;
    z-index: 2;
}

.d-block {
    display: block;
}

