.notification-banner {
  padding: 26px 30px;
  border: 1px solid var(--var-lightergray-color);
  border-radius: 9px;
  color: var(--var-active-color);
  display: inline-block;
  box-shadow: 0px 2px 2px 1px var(--tile-border-color);
}
.notification-banner a {
  outline: none;
  background: transparent;
  border: none;
  color: var(--var-active-color);
  font-size: 18px;
  font-weight: 500;
}
.notification-banner img,
.notification-banner p {
  display: inline-block;
  margin-right: 20px;
  font-size: 25px;
  font-weight: 500;
  vertical-align: middle;
}
.notif-left {
  display: inline-block;
  vertical-align: middle;
}
.notif-right {
  display: inline-block;
  vertical-align: middle;
  text-align: right;
  float: right;
}
.message-widget .notification-banner {
  margin-bottom: 23px;
}
.message-widget .notification-banner:last-child {
  margin-bottom: 35px;
}
.notification-banner .notif-close img {
  width: 18px;
  margin: 0;
  margin-left: 20px;
}
