

.notfound-widget {
    color: var(--var-notfoundText-color);
    font-size: 18px;
    text-align: center;
    border-top: 2px solid var(--var-transactionBorder-color);
    padding-top: 43px;
}
.notfound-widget img {
    width: 95px;
    margin-bottom: 30px;
}
.notfound-widget a{
    color: var(--var-notfoundLink-color);
    margin-top: 10px;
    display: inline-block;
}
.notfound-widget a:hover{
    color: var(--var-notfoundLink-color);
}
.notfound-widget p {
    margin-bottom: 10px;
}