.bg1{
  background:url(../../Assets/Image/bg-image1.png)no-repeat center center fixed; 
}
.bg2{
  background:url(../../Assets/Image/bg-image2.jpeg)no-repeat center center fixed; 
}
.bg3{
  background:url(../../Assets/Image/bg-image3.jpeg)no-repeat center center fixed; 
}
.bg4{
  background:url(../../Assets/Image/bg-image4.jpeg)no-repeat center center fixed; 
}

.login-bg{
  position: absolute;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  width: 100%;
  height: 100vh;
}


.login-left {
  display: inline-block;
  width: 100%;
  background-color: var(--var-loginHeader-color);
  border-radius: 15px 15px 0 0;
}

.login-right{
  background: rgba(255,255,255,0.95);
  border-radius: 0 0 15px 15px;
}

.login-right input {
  box-sizing: border-box;
  width: 100%;
  border: 1px solid var(--var-borderInput-color);
  background-color: var(--var-bgInput-color);
  color: var(--var-textInput-color);
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 22px;
  padding: 9px 21px;
  margin: 12.5px 0;
  border-radius: 21px;
}


.login-left-body{
  padding: 25px;
  text-align: center;
}
.login-left-body > svg, .login-left-body > img {
  height: 40px;
  margin: auto;
}
.login-right-body {
  padding: 30px 76px;
  text-align: center;
}

.submit-button {
  background-color: var(--var-loginActive-color);
  border: none;
  display: inline-block;
  padding: 9.5px 28px;
  border-radius: 25.28px;
  color: var(--var-white-color);
  width: auto;
  font-size: 18px;
  transition: 0.3s;
  line-height: 22px;
}

.submit-button:hover {
  background-color: var(--var-loginActiveHover-color);
}

.login-text {
  line-height: 34px;
  font-size: 24px;
  color: var(--var-loginWelcomeText-color);
  margin-bottom: 12px;
  font-weight: 500;
}

.message-text {
  color: var(--var-login-text);
  font-size: 12px;
  display: inline-block;
  line-height: 18px;
}

.button-group {
  margin-top: 17px;
  text-align: center;
}

.login-message {
  margin-top: 30px;
}

.message-text > a{
  color: var(--var-loginActive-color);
  text-decoration: underline;
}

.message-text > a:hover{
  color: var(--var-loginActiveHover-color);
  text-decoration: underline;
}

.message-text:last-child{
  margin-top: 15px;
}

.loading-page:after {
  background: rgba(156, 156, 156, 0.5);
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9;
}


@media only screen and (max-width: 480px)  {
  span.username:after {
    position: relative;
  }
  .login-right-body {
    padding: 38px 10%;
  }
  .login-text {
    line-height: 28px;
    font-size: 18px;
  }
  .login-right input {
    font-size: 14px;
    padding: 7px 15px;
    margin: 8px 0;
  }
  .login-left-body {
    padding: 18px;
  }
  .message-text {
    max-width: 250px;
  }
}