.upper-dashlet, .notification-banner, .lower-dashlet {
  margin-bottom: 30px;
  width: 100%;
}
.upper-dashlet, .lower-dashlet{
  display: flex;
  flex-wrap: wrap;
}

.notification-banner{
  padding: 26px 30px;
  border: 1px solid var(--tile-border-color);
  border-radius: 9px;
  color: var(--var-textNotif-color);
  display: inline-block;
}
.notification-banner button {
  outline: none;
  background: transparent;
  border: none;
  color: var(--var-textNotif-color);
  font-size: 18px;
  line-height: 30px;
  font-weight: 500;
}
.notification-banner img, .notification-banner p {
  display: inline-block;
  margin-right: 20px;
  font-size: 25px;
  font-weight: 500;
  vertical-align: middle;
}

/* .lower-dashlet .transactions-widget-customer, .lower-dashlet .transactions-widget-order-id {
  max-width: 315px;
} */
td.transactions-widget-link {
  max-width: 25px;
}
.transactions-widget-amount   label.widget-name {
  text-transform: uppercase;
}
@media screen and (max-width: 1650px) and (min-width: 1024px)  {
  .op-tile-2x {
    width: calc(100% - 33.33% - 15px);
  }
}

@media screen and (max-width: 1650px) {
  .upper-dashlet .op-tile {
    width: calc(25% - 12px);
    margin-right: 15px;
  }
  .op-tile-graph {
    width: 33.33%;
    margin-left: 15px;
  }
  .upper-dashlet .op-tile {
    padding: 15px 20px 40px;
  }
  .op-tile-footer {
    padding: 0 20px 20px;
  }
  .widget-count {
    font-size: 40px;
    line-height: normal;
    margin-bottom: 10px;
  }
  .simple-bar-graph-amount > span:last-child {
    font-size: 20px;
  }
  .simple-bar-graph-amount > span:first-child, .op-tile-footer, .transactions-widget label, .transactions-widget td > p {
    font-size: 14px;
  }
  .transaction-name .name {
    font-size: 17px;
  }
  .transactions-widget td > p {
    line-height: 25px;
  }
  .op-tile-2x, .op-tile-graph-head {
    padding: 20px 20px 0;
  }
  .transactions-widget td {
    padding: 10px 5px;
  }
  .upper-dashlet, .notification-banner, .lower-dashlet {
    margin-bottom: 20px;
  }
  .op-page {
    padding: 20px 10px 0;
  }
}


@media screen and (max-width: 1250px){
  .lower-dashlet .transactions-widget td, .lower-dashlet .transactions-widget-customer, .lower-dashlet .transactions-widget-order-id {
      width: 100%;
  }
  .op-tile-graph {
    order: 2;
    width: 350px;
    margin-left: 0;
  }
  .op-tile-2x {
    order: 1;
    width: 100%;    
    margin-bottom: 35px;
  }
}

@media screen and (max-width: 1099px){
  
  .notification-banner p {
    font-size: 20px;
  }
  .notification-banner a {
    font-size: 15px;
  }
  .notification-banner img {
    width: 25px;
  }
  .notification-banner {
    padding: 20px 25px;
  }
  .notification-banner button {
    line-height: normal;
  }
}

@media screen and (max-width: 875px){
  .notification-banner, .op-tile-2x, .upper-dashlet{
    margin-bottom: 25px;
  }
  .transactions-widget td {
    display: inline-block;
    margin-bottom: 0;
    padding: 0;
  }
  .transactions-widget tr {
    display: inline-block;
  }
  .op-tile-2x .transactions-widget tr:last-child {
    padding-bottom: 22px;
  }
  td.transactions-widget-order-id {
    padding: 10px 0 20px;
  }
}

@media screen and (max-width: 767px){
  .upper-dashlet .op-tile {
    width: calc(50% - 10px);
    margin: 0 20px 20px 0;
  }
  .upper-dashlet .op-tile:nth-child(2n) {
    margin-right: 0;
  }
  .upper-dashlet .op-tile:last-child, .upper-dashlet .op-tile:nth-last-child(2) {
    margin-bottom: 0;
  }
  .notification-banner, .op-tile-2x, .upper-dashlet{
    margin-bottom: 20px;
  }
  .notification-banner p {
    font-size: 17px;
  }
  .notification-banner img {
    width: 20px;
  }
  .notification-banner {
    padding: 18px 15px;
  }
  .notification-banner a {
    font-size: 13px;
  } 
  .tab-menu li {
    margin-right: 30px;
  }
  .op-tile-graph {
    min-height: auto;
  }
  .op-tile-graph-chart {
    position: relative;
  }
}

@media screen and (max-width: 570px){
  .notification-banner img {
    margin-right: 10px;
  }
  .notification-banner p {
    margin-right: 0;
  }
  .upper-dashlet .op-tile {
    width: calc(50% - 5px);
    margin: 0 10px 10px 0;
  }
  .notification-banner p {
    width: calc(100% - 50px);
    line-height: 20px;
  }
  
  .op-tile-graph {
    order: 1;
    width: 100%;
    margin-bottom: 20px;
  }
  .op-tile-2x {
    order: 2;
    margin-bottom: 0;
  }
  
}

@media screen and (max-width: 540px){
  .op-tile-2x, .op-tile-graph-head {
    padding: 20px 15px 0;
}
  .op-tile-2x .transaction-box {
    max-height: 100vh;
  }
  .transaction-report .op-tile-2x-head {
    padding-top: 18px;
  }
  .transactions-widget tr {
    position: relative;
  }
  td.transactions-widget-link {
    position: absolute;
    bottom: 35px;
    right: 0;
  }
  .op-tile-2x .transactions-widget tr:last-child {
    padding-bottom: 25px;
  }
  .op-tile-2x-head p {
    width: 60px;
  }
  .op-tile-2x-title {
    width: calc(100% - 60px);
  } 
  .op-tile-2x:after{
    display: none;
  }
}

@media screen and (max-width: 480px){
  .op-tile-graph-head {
    padding: 20px 15px;
  }
  
  .upper-dashlet .op-tile {
    width: 100%;
    margin: 0 0 15px 0;
  }
  .upper-dashlet .op-tile:nth-last-child(2), .notification-banner, .op-tile-2x, .upper-dashlet {
      margin-bottom: 15px;
  }
  .op-tile-graph {
    margin-bottom: 15px;
  }
  .op-navbar img {
    max-width: 150px;
  }
  .navbar-right .username {
    font-size: 12px;
  }
  ul.sub-icons li {
    margin-left: 20px;
  } 
  .sub-icons li:first-child {
    margin-left: 0;
  }
  .sub-header .navbar-right {
    width: 75px;
  }
  .sub-header .navbar-left {
    width: calc(100% - 75px);
  }
  .tab-menu li {
    margin-right: 15px;
  }
}


@media screen and (max-width: 380px){
  .op-page {
    padding: 20px 0;
  }
  .transactions-widget tr {
    padding: 22px 5px;
}
  .op-tile-2x-title, .op-tile-graph-title {
    font-size: 17px;
  }
  .tab-menu li {
    font-size: 14px;
    padding: 0 5px;
  }
  ul.sub-icons img {
    width: 20px;
  }
  .sub-header .navbar-right {
    width: 60px;
  }
  .sub-header .navbar-left {
    width: calc(100% - 60px);
  }
  .tab-menu li {
    margin-right: 0;
  }
  ul.sub-icons li {
    margin-left: 10px;
  }
}
