.transaction-report {
    border: 2px solid var(--tile-border-color);
    border-radius: 10px;
    color: var(--var-text-color);
    position: relative;
    padding: 30px 2px 30px 28px;
    width: 100%;
    height: 100%;
}

.transaction-report-header{
    padding-top: 30px;
}
.transaction-report-header > span {
    font-size: 23px;
    font-weight: 600;
    display: inline-block;
}
.transaction-datepicker{
    display: inline-block;
    margin-left: 22px;
    position: relative;
}
.transaction-datepicker input {
    background: transparent;
    border: 1px solid var(--var-datePicker-color);
    font-size: 12px;
    padding: 7px;
    border-radius: 5px;
    color: var(--var-datePicker-color);
    cursor: pointer;
    padding-right: 35px;
    width: 160px;

}
.transaction-box {
    height: calc(100vh - 220px);
    padding-right: 20px;
    width: calc(100% - 12px);
}
.transaction-report:after {
    content: '';
    position: absolute;
    width: 1px;
    height: 100%;
    right: 14px;
    background: var(--tile-border-color);
    top: 0;
}
.transaction-datepicker .react-datepicker__input-container:before {
    content: '';
    position: absolute;
    right: 31px;
    top: 4px;
    width: 1px;
    background: var(--var-datePicker-color);
    height: calc(100% - 8px);
}
.transaction-datepicker .react-datepicker__input-container:after {
    content: '\f133';
    font-family: fontAwesome;
    font-weight: 300;
    right: 10px;
    top: 6px;
    position: absolute;
    font-size: 15px;
    pointer-events: none;
}
.transaction-close {
    position: absolute;
    right: 0;
    top: 0;
}
.transaction-close i.fa.fa-times {
    font-weight: 100;
    font-size: 30px;
    outline: none;
    color: var(--var-transactionBorder-color);
}
.transaction-report .op-tile-2x-title {
    width: 100%;
    position: relative;
}
.react-datepicker-popper{
    transform: translate3d(0, 30px, 0px) !important;
}