.row-column {
  display: inline-block;
  vertical-align: top;
  width: 15%;
}

.line {
  margin-bottom: 8px;
  font-size: 12px;
}

/* SET COLUMN WIDTHS */
.transaction-customer-icon {
  width: 64px;
}
.customer-info {
  min-width: 250px;
}
.transaction-ids {
  min-width: 240px;
}
.transaction-results {
  min-width: 200px;
}
.transaction-payment {
  min-width: 64px;
  text-align: center;
}
.transaction-amount {
  float: right;
}

.customer-icon {
  width: 48px;
}

.customer-name {
  font-size: 16px;
  font-weight: 600;
}

.customer-email {
  color: var(--var-accent-color);
}

.line strong {
  display: inline-block;
  width: 130px;
  text-transform: uppercase;
  text-align: right;
  padding-right: 10px;
  font-weight: 600;
}

.line em {
  font-size: 15px;
}

.transaction-amount {
  text-align: right !important;
  font-size: 32px;
  margin-right: 20px;
}
.transaction-amount small {
  font-size: 14px;
}

.transaction-report .op-tile-2x {
  border: none;
  border-radius: 0;
  padding: 0;
  width: 100%;
  height: calc(100vh - 220px);
  padding-right:40px;
}

.react-datepicker-wrapper {
  margin-bottom: 10px;
}
.widget-filter {
  font-size: 12px;
  border-radius: 5px;
  padding: 5.5px 15px;
  color: var(--var-filterText-color);
  display: inline-block;
  background: var(--tile-border-color);
}
.widget-filter.Sales {
  background: var(--var-dashletSales-color);
}
.widget-filter.Fraudelent {
  background: var(--var-dashletFraud-color);
}
.widget-filter.Chargeback {
  background: var(--var-dashletChargebacks-color);
}

.react-datepicker__tab-loop {
  position: absolute;
  top: 0;
}
.widget-filter span img {
  width: 10px;
  margin: 4.5px 0px;
}
.widget-filter span {
  background: var(--var-white-color);
  border-radius: 50%;
  width: 18.5px;
  margin-left: 10px;
  height: 18.5px;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
}
.widget-filter p {
  font-size: 12px;
  font-weight: 700;
  width: auto;
  vertical-align: middle;
  color: var(--var-filterText-color);
}

@media screen and (max-width: 540px){
  .transaction-report .op-tile-2x {
    padding-right: 0;
  }
}