/* global settings */
body {
  font-family: 'Roboto', sans-serif;
}

/* header standards */
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}
h1 {
  font-size: 32px;
}
h2 {
  font-size: 28px;
}
h3 {
  font-size: 24px;
}
h4 {
  font-size: 20px;
}
h5 {
  font-size: 18px;
}
h6 {
  font-size: 16px;
}

/* links */
a,
a:hover {
  text-decoration: none;
  color: inherit;
}

* {
  box-sizing: border-box;
}

/* CONTAINERS */
/* page container */

.container {
  max-width: 1920px;
  padding: 0 115px;
  margin: auto;
  position: relative;
}

.hidden {
  display:none;
}
.show{
  display:block;
}

i.fa {
  font-family: fontAwesome;
  color: var(--var-primary-color);
}

@media screen and (max-width: 1350px){
  .container {
    padding: 0 50px;
  }
}
@media screen and (max-width: 1099px){
  .container {
    padding: 0 30px;
  }
}
@media screen and (max-width: 875px){
  .container {
    padding: 0 15px;
  }
}